import {Component, ViewChild} from '@angular/core';
import {
    IonicPage,
    NavController,
    NavParams,
    Slides,
    ModalController,
    Select,
    AlertController
} from 'ionic-angular';
import {CheckinServiceProvider} from "../../providers/checkin-service/checkin-service";
import {StorageManagerProvider} from "../../providers/storage-manager/storage-manager";
import {Process} from "../checkin/conditions";
import {Content} from 'ionic-angular';
import {Node} from "../../providers/checkin-service/checkin.modals";
import {DrawProvider} from "../../providers/draw/draw";
import {AuthManagerProvider} from "../../providers/auth-manager/auth-manager";
import {ApiProvider} from "../../providers/api/api";
import {Info} from "../../services/statics";
import {HomePage} from "../home/home";
import {LinkManagerProvider} from "../../providers/link-manager/link-manager";

/**
 * Generated class for the CheckinForDoctorsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-checkin-for-doctors',
  templateUrl: 'checkin-for-doctors.html',
})
export class CheckinForDoctorsPage {
    @ViewChild(Slides) slides: Slides;
    @ViewChild(Content) content: Content;
    @ViewChild(Select) patientEmailPopUp: Select;


    private counter = 0;

    private data = [];
    loaded = false;
    tabs: string;
    currentSlide = 0;
    activeDot = 1;
    elementNr = 0;
    speed = 1000;
    currentTab = 0;
    currentPercent = 0;
    currentPercentString: string;
    privacyChecked = false;
    agbChecked = false;
    finishedPressed = false;

    isMedical = this.authManager.isMedical;
    patientsEmailsOptions : any;
    patientEmails = [];
    doctorIsAtStart = true;
    selectedPatientEmail = "";
    informationForDoctors = "Informationen für die Patientenaufnahme";
    loadingText = "";

    ngAfterViewInit() {

    }


    rulesFullFilled(condition, item) {
        return Process.rulesFullFilled(condition, item, this.getVals())
    }

    constructor(public navCtrl: NavController, public navParams: NavParams, private checkinService: CheckinServiceProvider, private storageProv: StorageManagerProvider, private modelCtrl: ModalController, private drawManager: DrawProvider, private authManager: AuthManagerProvider, private api: ApiProvider, private alertCtrl: AlertController, private linkManager: LinkManagerProvider) {
        this.patientsEmailsOptions = {
            title: 'E-Mail des Patienten auswählen',
            subTitle: 'E-Mail auswählen',
            okText: 'anzeigen',
            cancelText: 'abbrechen',
            placeholder: "E-Mail des Patienten auswählen",
            cssClass: "doctor-checkin-popover-email"
            // mode: 'md'
        };

        this.doctorIsAtStart = true;
        this.loaded = true;
        this.tabs = 'p' + this.currentTab;
        this.data = this.checkinService.presetData;
        this.initializePatientEmailsForCheckins();
    }

    openHomePage() {
      this.navCtrl.setRoot(HomePage);
    }

    onRadioChange(item: Node) {

        item.reflector = "";
    }

    get isIE() {
        return this.checkinService.isIE;
    }
    showAgb(agbs) {
        if(agbs) {

            this.modelCtrl.create('TextPage', {html: true, closeable: true, target: this.linkManager.getNutungsbedingungenAsHalJSON()}).present();

            return;
        }

        this.modelCtrl.create('TextPage', {html: true, closeable: true, target: this.linkManager.getDatenschutzAsJSON()}).present();
    }
    get currentTitle() {
        return this.data[this.currentTab] != null ? this.data[this.currentTab].title : "Nicht definiert";
    }

    get currentCount() {
        return this.data[this.currentTab] != null ? this.data[this.currentTab].children.length : 0;
    }

    get tabCount() {
        return this.data != null ? this.data.length : 0;
    }

    showMessage(title: string, message: string, callback = (data) => { console.log("On the message with title " + title + " and messsage '" + message + "'", data)} ) {

        let alert = this.alertCtrl.create({
            title: title,
            subTitle: message.replace("\n","<br>"),
            buttons: [
                {
                    text: 'OK',
                    handler: data => {
                        callback(data)
                    }
                }
            ]
        });
        alert.present();
    }

    submitData() {
        let alert = this.alertCtrl.create();
        const alertTitleMessage = "Datenbankübernahme";
        alert.setTitle(alertTitleMessage);
        alert.addInput({
            type: 'radio',
            label: 'Patientendaten freigeben',
            value: 'Patientendaten freigeben'
        });
        alert.addInput({
            type: 'radio',
            label: 'Patientendaten unwiderruflich löschen',
            value: 'Patientendaten unwiderruflich löschen'
        });
        alert.addButton('Abbrechen');
        alert.addButton({
            text: 'Bestätigen',
            handler: data => {
                console.log("Data is: ", data);
                this.loadingText = "Daten verschlüsseln und versenden";
                this.loaded = false;
                this.doctorIsAtStart = true;
                if (data == "Patientendaten freigeben") {
                    let dataToSend = this.checkinService.prepareForSend(this.data, this.getVals());
                    console.log("Checkin data to send to finalize: ", dataToSend);
                    this.checkinService.sendCheckinToKIS(dataToSend, this.selectedPatientEmail, (success, response) => {
                        if (success) {
                            this.finishedClicked();
                        }
                        else {
                            this.showMessage("Fehler beim Speichern der Daten auf DB", "Fehler bei der Übermittlung der Registrierungsdaten an die Krankenhausdatenbank.\n" + response); // "Error storing the data to DB", "Error when submitting the registration data to the hospital database.\n"
                            this.loaded = true;
                            this.initializePatientEmailsForCheckins();
                        }
                    });
                }
                if (data == "Patientendaten unwiderruflich löschen") {
                    this.checkinService.deleteCheckins(this.selectedPatientEmail, (success, response) => {
                        this.initializePatientEmailsForCheckins();
                        if (success == false) {
                            this.showMessage("Fehler beim Löschen", "Fehler beim Löschen von Datensätzen vom Server: " + response); // "Deletion error", "Error deleting records from server: "
                            this.loaded = true;
                        }
                        else {
                            this.showMessage("Löschen erfolgreich", "Aufzeichnungen wurden komplett aus dem System gelöscht."); // "Deletion successful", "Records were deleted completely from the system."
                            this.loaded = true;
                        }
                    });
                }
            }
        });
        alert.present();
    }

    selectedPatient(data) {
        console.log("Email selected: ", data["email"])
        this.selectedPatientEmail = data["email"];
        this.showPatientData(this.selectedPatientEmail)
        this.doctorIsAtStart = true;
        this.content.scrollTo(0, 0, 0);
    }

    initializePatientEmailsForCheckins() {

        let apiPath = "/search/GetPatientsEmailsWCheckins";

        this.api.post(apiPath, {key: this.authManager.key}, [], (success, data) => {
            if(success) {
                let bodyResponse = JSON.parse(data["_body"]);
                let successfullyTransferred = bodyResponse["success"];
                let dataResponse = bodyResponse["data"];
                if (successfullyTransferred) {
                    console.log("The response was: ", dataResponse);
                    this.patientEmails = dataResponse;
                }
                else {
                    console.log("There was a problem with the server.", dataResponse);
                    if (dataResponse=="unable_to_store_pdf_successfully") {
                        console.log("The server had a problem and couldn't store the PDF file successfully");
                    }
                }
            }
            else {
                console.log("The server was most likely un-responsive. Please see details: ", success, data);
            }
        });

    }

    /**
     *
     * @param patientsEmails string with emails seperated by commas. E.g. "first@my.com,second@my.com"
     */
    showPatientData(patientsEmails) {
        this.loaded = false;
        this.loadingText = "Herunterladen und Entschlüsseln von Daten";
        this.checkinService.retrieveCheckins(patientsEmails,(success, data)=> {
            if ( success ) {
                let listOfcheckins = atob(data);
                console.log("List of checkins JSON: ", JSON.parse(listOfcheckins));
                listOfcheckins = JSON.parse(listOfcheckins);

                this.loadingText = "Verarbeitung und Formatierung von Daten";
                for(let entry of listOfcheckins) {
                    let user = entry["username"];
                    console.log("we have checkin info for user: ", user);
                    let checkinData = JSON.parse(JSON.parse(decodeURIComponent(atob(entry["data"]))));


                    this.checkinService.parseData(checkinData, (success) => {
                        this.currentTab = 1;
                        this.tabs = 'p' + this.currentTab;
                        this.data = this.checkinService.presetData;
                        this.doctorIsAtStart = false;
                        this.loaded = true;
                        this.loadingText = "";
                        console.log("After parsing the data.", this.data);
                        console.log("This checking preset data", this.checkinService.presetData);
                    });

                }
            }
            else {
                this.showMessage("Fehler bei der Anzeige von Patientendaten", "Es gab einen Fehler bei der Anzeige der Patientendaten: " + data); // "There was an error showing the patient data: "
                console.log("There was a problem with the checkins retrieval from the server.")
            }
        });
    }

    finishedClicked() {
        this.finishedPressed = true;
        /* const data = this.checkinService.prepareForSend(this.data);*/

        const data = this.getVals();

        console.log("Data", data);
        console.log("Checkin data in form: ", this.data);

        this.checkinService.sendCheckin(data, this.selectedPatientEmail, (success, data) => {
            this.loaded = true;
            this.initializePatientEmailsForCheckins();
            if (success) {
                this.showMessage("Die Freigabe des Patienten ist erfolgt.", "");
            }
            else {
                this.showMessage("Fehler beim Speichern der Daten in der CDB", "Fehler bei der Übermittlung der Registrierungsdaten an die Datenbank.\n" + data); // "Error storing the data to CDB", "Error when submitting the registration data to the database.\n"
            }
        });
    }

    onSegmentChange(i) {

        this.currentTab = i;
        this.tabs = 'p' + this.currentTab;
        this.currentSlide = 0;
        this.content.scrollTo(0, 0, 0);

        this.currentPercent = this.currentSlide / this.currentCount * 100;
        this.currentPercentString = this.currentPercent + "%";
    }

    keyDownFunction(event, elementNr, totalLength) {
        if (event.keyCode == 13 || event.keyCode == 40) {
            let wanted = elementNr + 1;
            if (wanted === totalLength) {
                this.onSegmentChange(this.currentTab + 1)
            } else {
                this.nextElement(elementNr, totalLength)
            }
        }
        if (event.keyCode == 38) {
            let wanted = elementNr - 1;
            if (wanted === totalLength) {
                this.onSegmentChange(this.currentTab - 1)
            } else {
                this.prevElement(elementNr)
            }
        }
    }

    nextElement(elementNr, totalLength) {
        let wanted = elementNr;
        if (wanted > totalLength) {
            return;
        }
        let next:any = {};
        next = document.getElementById('element-' + wanted).parentElement.nextSibling;
        while (next.children[0] == undefined) {
            next = next.nextSibling;
        }
        let yOffset = next.offsetTop;
        this.content.scrollTo(0, yOffset - 50, this.speed);

        this.currentSlide = Number.parseInt(next.children[0].getAttribute('id').substr(8));
        this.currentPercent = this.currentSlide / this.currentCount * 100;
        this.currentPercentString = this.currentPercent + "%";

    }

    prevElement(elementNr) {

        let wanted = elementNr - 1;
        if (wanted < 0) {
            return;
        }
        let previous:any = {};
        previous = document.getElementById('element-' + elementNr).parentElement.previousSibling;
        while (previous.children[0] == undefined) {
            previous = previous.previousSibling;
        }
        let yOffset = previous.offsetTop;
        this.content.scrollTo(0, yOffset - 50, this.speed);

        this.currentSlide = Number.parseInt(previous.children[0].getAttribute('id').substr(8));

        // if (document.getElementById('element-' + wanted) != null) {
        //
        //     let yOffset = document.getElementById('element-' + wanted).offsetTop;
        //     this.content.scrollTo(0, yOffset - 50, this.speed);
        // }
        // this.currentSlide = wanted;
        this.currentPercent = this.currentSlide / this.currentCount * 100;
        this.currentPercentString = this.currentPercent + "%";
    }

    regionBack() {


        this.currentSlide = this.slides.getActiveIndex();
        this.slides.getActiveIndex();
        this.slides.slidePrev();
        this.slides.update();

        this.content.scrollToTop(150);

    }

    regionForward() {


        this.currentSlide = this.slides.getActiveIndex();
        this.slides.getActiveIndex();
        this.slides.slideNext();
        this.slides.update();

        this.content.scrollToTop(150);


    }

    getPreviewData(path: string) {

        if (this.drawManager.processed[path] != null) {
            return this.drawManager.processed[path];
        }
        return path
    }

    slidePrev(elementNr) {
        let wanted = elementNr - 1;
        if (wanted < 0) {
            return;
        }
        if (document.getElementById('element-' + wanted) != null) {

            let yOffset = document.getElementById('element-' + wanted).offsetTop;
            this.content.scrollTo(0, yOffset, this.speed);
        }
        this.currentSlide = wanted;
    }

    private appendOther(parent: Node) {

        const newArr = [];

        for (let i = 0; i < parent.children.length; i++) {
            const currentItem = parent.children[i];

            if (currentItem.selfMultiply && currentItem.id.indexOf("child-") == -1) {
                this.counter--;
                const node = new Node(currentItem.name + "-" + newArr.length, currentItem.title, currentItem.type, currentItem.id + "child-" + this.counter);
                for (let y = 0; y < currentItem.children.length; y++) {
                    this.counter--;
                    const copyNode = currentItem.children[y];
                    const nNode = new Node(copyNode.name, copyNode.title, copyNode.type, copyNode.id + "-child-" + this.counter);
                    const fieldKeys = copyNode.getFieldKeys();
                    for (let x = 0; x < fieldKeys.length; x++) {
                        nNode.addField(fieldKeys[i], copyNode.getField(fieldKeys[i]));
                    }

                    node.children.push(nNode);
                }

                const fieldKeys = currentItem.getFieldKeys();
                console.log(fieldKeys);
                for (let x = 0; x < fieldKeys.length; x++) {
                    if (node.type == "checkboxes" && fieldKeys[x] == "values") {

                        const vals = currentItem.getField("values");
                        const arr = [];
                        this.counter--;
                        vals.forEach((elem) => {

                            arr.push({val: elem.val, ref: elem.ref + "-" + this.counter})

                        });
                        node.addField("values", arr);
                        continue
                    }
                    node.addField(fieldKeys[x], currentItem.getField(fieldKeys[x]));
                }

                //node.parent = parent;
                newArr.push(node);
            }
        }
        parent.subGroupCount++;
        this.counter--;
        const deleter = new Node("del_group", "Gruppe Löschen", "group_del", "del-" + parent.id + "child-" + this.counter);
        deleter.addField("id", parent.subGroupCount);
        newArr.unshift(deleter)
        parent.subGroups[parent.subGroupCount] = newArr;
        for (let y = 0; y < newArr.length; y++) {
            parent.children.push(newArr[y]);
        }


        console.log(this.data)
    }

    delSubGroup(parent: Node, id: number) {
        const tArr = parent.subGroups[id];
        for (let i = 0; i < tArr.length; i++) {
            const cId = tArr[i].id;
            for (let x = 0; x < parent.children.length; x++) {
                const current = parent.children[x];
                if (current.id == cId) {
                    parent.children.splice(parent.children.indexOf(current), 1);
                }
            }
        }
    }

    isEnd(elementNr, total) {

        const id = elementNr + 1 == total;

        return id;
    }

    printVals() {
        const data = this.getVals();
        console.log(data);
        this.storageProv.save(this.authManager.encodedEmailId + "checkin_data", data, () => {
        });

    }

    getVals() {
        const arr = [];
        this.data.forEach((it => {
            this.printNode(it, arr)
        }))

        return arr;
    }

    ionViewWillLeave() {
        this.printVals();
    }

    private showDrawer(path) {
        this.modelCtrl.create('DrawPage', {img: path}).present();
    }

    printNode(node, array) {
        if (node.children != null) {
            for (let i = 0; i < node.children.length; i++) {
                const child = node.children[i];
                this.printNode(child, array);
            }
        }
        if (node.type != "checkboxes") {
            array.push({key: node.name, value: node.reflector, id: node.id});
        } else {
            array.push({key: node.name, value: node.getField("values"), id: node.id});

        }
    }

    isBeginning() {
        if (this.slides != null) {
            return this.slides.isBeginning();
        }
        return true;
    }

}



